.item{
width: 230px;
}
.item p{
    margin: 8px 0;
    font-size: 15px;
    font-weight: 700;
}
.item img{
    width: 230px;
}
.item-price{
    font-size: 15px;
    color: black;
    font-weight: 600;
}
.item:hover{
  transform: scale(1.05);
  transition: 0.6s;  
}
.item button{
    width: 70px;
    height: 30px;
    background-color: orangered;
    color: white;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    border: none;
    border-radius: 2px;
}
.item button:hover{
    background: orange;
    transform: scale(1.05);
    transition: 0.6s;
    color: black;
}